.devices {
  background-image: url("bcg_mountains.e1efafc9.png");
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 44px 16px 24px;
  display: flex;
  position: relative;
}

.devices__ipad-inner-big {
  display: none;
}

.devices:after {
  content: " ";
  z-index: 1;
  background-color: #fffc;
  position: absolute;
  inset: 0;
}

.devices__title {
  z-index: 3;
  height: 82px;
  font-size: 30px;
  position: relative;
}

.devices__subtitle {
  z-index: 3;
  align-items: center;
  height: 84px;
  margin-bottom: 24px;
  font-size: 36px;
  display: flex;
  position: relative;
}

.devices__separator {
  z-index: 3;
  margin-bottom: 36px;
  position: relative;
}

.devices__content {
  z-index: 3;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: fit-content;
  height: fit-content;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.devices__ipad {
  width: 286px;
  height: 218px;
}

.devices__ipad-inner {
  width: 274px;
  height: 206px;
  position: absolute;
}

.devices__content-iphone {
  width: fit-content;
  height: fit-content;
}

.devices__img-phone {
  z-index: 3;
  width: 84px;
  height: 174px;
  position: absolute;
  top: 98px;
  right: 28px;
}

.devices__img-phone__inner {
  border-radius: 12px 13px;
  width: 76px;
  height: 164px;
  position: absolute;
  top: 100px;
  right: 32px;
}

@media (width >= 768px) {
  .devices {
    background-image: url("bcg_mountains.d7e034ba.png");
    padding: 28px 116px 38px;
  }

  .devices__separator {
    margin-bottom: 40px;
  }

  .devices__title {
    height: 54px;
  }

  .devices__subtitle {
    height: 54px;
    margin-bottom: 16px;
  }

  .devices__ipad {
    z-index: 2;
    width: 460px;
    height: 336px;
  }

  .devices__ipad-inner {
    z-index: 1;
    display: none;
  }

  .devices__ipad-inner-big {
    z-index: 1;
    width: 410px;
    height: 310px;
    display: block;
    position: absolute;
  }

  .devices__img-phone {
    width: 134px;
    height: 278px;
    top: 134px;
    right: -32px;
  }

  .devices__img-phone__inner {
    z-index: 2;
    width: 124px;
    height: 264px;
    top: 136px;
    right: -28px;
  }
}

@media (width >= 1300px) {
  .devices {
    padding: 42px 170px 56px;
  }

  .devices__separator {
    margin-bottom: 60px;
  }

  .devices__title {
    height: 80px;
  }

  .devices__subtitle {
    height: 80px;
    margin-bottom: 24px;
  }

  .devices__ipad {
    z-index: 2;
    width: 660px;
    height: 494px;
  }

  .devices__ipad-inner-big {
    z-index: 1;
    width: 618px;
    height: 452px;
  }

  .devices__img-phone {
    width: 200px;
    height: 410px;
    top: 212px;
    right: -32px;
  }

  .devices__img-phone__inner {
    z-index: 2;
    width: 180px;
    height: 390px;
    top: 216px;
    right: -22px;
  }
}
